.App{
  background-color: #487ECE;
  width: 518px;
  min-height: 704px;
  margin-left: auto;
  margin-right: auto;
}

h1{
  color: white;
  text-align: center;
}

h2{
  /* background-color: #d42525; */
  max-height: 25px;
  overflow: hidden;
  margin-left: 10px;
}

h5{
  margin-top: 0px;
}

.habit{
  width: 492px;
  height: 98px;
  display: flex;
  border-radius: 10px;
  background-color: #D9D9D9;
  margin-left: auto;
  margin-right: auto;
  justify-content: space-around;
  margin-top: 10px;
  margin-bottom: 10px;
}

.subContainer{
  display: flex;
  margin-left: 10px;
}

/* previous Day */

.previousDayContainer{
  display: flex;
  width: 160px;
  justify-content: space-between;
  height: 17px;
  margin-left: 20px;
}

.previousDayTrack{
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background-color: #25d44c;
}

/* Done and not done button */
.btnContainer{
  display: flex;
  width: 180px;
  justify-content: space-between;
  margin-top: auto;
  width: 150px;
  margin-bottom: auto;
  margin-right: 10px;
  margin-right: -10px;
}

.threeDotLogo{
  margin-top: 10px;
  cursor: pointer;
}

.options{
  background-color: #487ECE;
  position: absolute;
  min-width: 70px;
  /* max-height: 50px; */
  margin-left: 370px;
  margin-top: 10px;
  padding: 5px;
}

.optionItem{
  text-align: center;
  border: 1px solid white;
  background-color: white;
  border-radius: 15px;
  margin-top: 4px;
}

.optionItem:hover{
  cursor: pointer;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.doneBtn{
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: #25d44c;
}

.notdoneBtn{
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: #d42525;
}

.unmarkedBtn{
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: #a6a6a6;
}


/* Add habit button */
.AddBtnDiv{
  border-radius: 50%;
  background-color: white;
  width: 80px;
  height: 80px;
  box-shadow: rgb(20, 26, 33) 0px 20px 30px -10px;
  position: fixed;
  bottom: 4rem;
  margin-left: 25px;
  cursor: pointer;
}

.AddBtn{
  font-size: 80px;
}

/* AddHabit Page */
.AddTaskContainer{
  display: flex;
  flex-direction: column;
  background-color: whitesmoke;
  width: 380px;
  margin: auto;
  margin-top: 50px;
  padding: 20px;
  border-radius: 10px;
  color: white;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.inputHabitField{
  height: 40px;
  width: 300px;
  border-radius: 8px;
  border: none;
  font-size: 16px;
  padding-left: 10px;
  padding-right: 10px;
  margin: auto;
  color: white;
  background-color: grey;
}

::placeholder{
  color: white;
}

.addButton{
  background-color: #25d44c;
  height: 40px;
  width: 130px;
  color: white;
  font-size: 16px;
  border-radius: 25px;
  border: none;
  margin: auto;
  margin-top: 20px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.addButton:hover{
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  cursor: pointer;
}

/* view Page */
.habitViewContainer{
  background-color: #D9D9D9;
  width: 470px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 15px;
  padding: 10px;
}

.dayblockContainer{
  display: flex;
  justify-content: space-between;
}

.dayblock{
  width: 56px;
  height: 46px;
  border-radius: 8px;
  background-color: white;
}

.totalContainer{
  display: flex;
  background-color: white;
  width: 167px;
  height: 24px;
  justify-content: space-around;
  border-radius: 8px;
}

.totalSubContainer{
  display: flex;
  padding-top: 3px;
}

.greyCircle{
  width: 18px;
  height: 18px;
  background-color: rgb(184, 184, 184);
  border-radius: 50%;
  margin-right:3px;
}

.greenCircle{
  width: 18px;
  height: 18px;
  background-color: green;
  border-radius: 50%;
  margin-right:3px;
}

.redCircle{
  width: 18px;
  height: 18px;
  background-color: #e30000;
  border-radius: 50%;
  margin-right:3px;
}

.changeStatusOfHabit{
  background-color: rgb(234, 234, 234);
  width: 230px;
  height: 70px;
  display: flex;
  justify-content: space-around;
  padding: 10px;
  margin: auto;
  margin-top: 20px;
  border-radius: 20px;
}